<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Kalkulasi Cuti Tahunan</b>
			</div>
		</div>
		<div class="container mt-6 mb-3 w-100">
			<div class="card bg-white shadow">
				<div class="card-body">
					<div class="row align-items-center">
						<div class="col ml--2">
							<div><b>Cuti Tahunan</b></div>
						</div>
						<div class="col-auto">
							<span class="line" style="width:10px;height:10px" v-if="isLoaded == false"></span>
							{{jumlahCutiTahunan.lama_cuti}} hari
						</div>
					</div>
				</div>
			</div>
			<div class="pt-3 pl-3 d-flex justify-content-between">
				<span>Ambil cuti</span>
				<hr width="60%" style="margin-top:11px;" class="mb--2">
			</div>
			<div class="clearfix"></div>
			<span class="line mt-3 mb-3" style="width:100%;height:30px;" v-if="isLoaded == false"></span>
			<span class="line mt-3 mb-3" style="width:100%;height:30px;" v-if="isLoaded == false"></span>
			<span class="line mt-3 mb-3" style="width:100%;height:30px;" v-if="isLoaded == false"></span>
			<div v-for="(cuti,i) in data.data">
				<a href="javascript:void(0)" class="card bg-white shadow mt-3" style="color:#333;" id="accordion" :data-controls="'#demo' + i" aria-expanded="false" data-toggle="collapse" :data-target="'#demo' + i">
					<div class="card-body">
						<div class="row align-items-center">
							<div class="col ml--2">
								<div><span class="text-success" v-if="cuti.status == 'disetujui'"></span><span class="text-warning" v-if="cuti.status == 'menunggu'"></span> {{cuti.status}}</div>
								<div style="font-size:11pt;" class="mt-1"><b>{{cuti.mulai_cuti}} <span v-if="cuti.selesai_cuti != null">-</span> {{cuti.selesai_cuti}}</b></div>
							</div>
							<div class="col-auto">
								- {{cuti.lama_cuti}} hari
							</div>
						</div>
					</div>
				</a>
				<div class="card bg-secondary p--1 collapse hide" style="border-radius:0px !important;width:95%;margin:0 auto;" :id="'demo' + i" :aria-labelledby="'#demo' + i"  data-parent="#accordion">
					<div class="card-body">
						<h5>Deskripsi :</h5>
						<p>
							{{cuti.deskripsi}}
						</p>
					</div>
				</div>
			</div>
			<hr class="mt-4 mb-4">
			<div class="card bg-white shadow">
				<div class="card-body">
					<div class="row align-items-center">
						<div class="col ml--2">
							<div><b>Sisa Cuti</b></div>
						</div>
						<div class="col-auto"> 
							<span class="line" style="width:10px;height:10px" v-if="isLoaded == false"></span> <span v-if="isLoaded">{{jumlahCutiTahunan.lama_cuti - totalAmbilCuti.total_ambil_cuti}}</span> hari
						</div>
					</div>
				</div>
			</div>
			<hr>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";
import $ from 'jquery';

export default{
	data(){
		return{
			isLoaded: false,
			data: '',
			jumlahCutiTahunan: '',
			totalAmbilCuti: '',
		}
	},
	methods:{
		
	},
	mounted(){
		if(localStorage.getItem('access') == "admin"){
			location.href = '/admin'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/kalkulasi-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				if(response.status == 200){
					console.log(response.data);
					this.data = response.data;
					this.jumlahCutiTahunan = response.data.jumlahCutiTahunan;
					this.totalAmbilCuti = response.data.totalAmbilCuti[0];
				}
			}).catch((error) => {
				console.log(error)
			});

			$("#accordion").on("hide.bs.collapse show.bs.collapse", e => {
				$(e.target)
				.prev()
				.find("i:last-child")
				.toggleClass("fa-minus fa-plus");
			});

		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		position:absolute;
		bottom: 15%;
		right: 6%;
		font-size:20pt;
		margin-top:0px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>