<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary pb-4">
			<div class="text-center p-4 text-white">
				{{selamat}},<span class="line" style="width:105px;" v-if="isLoaded == false"></span> {{ username }}
			</div>
		</div>
		<div class="container mt--4 mb-3 w-100">
			<div class="card bg-white  shadow">
				<div class="card-body text-center">
					<b> <span class="line" style="width:105px;" v-if="isLoaded == false"></span> {{ username }}</b>
					<hr class="mt-3 mb-3">
					<span class="line" style="width:105px;" v-if="isLoaded == false"></span>{{divisi}}
				</div>
			</div>
		</div>
		<div class="container">
			<div class="card mt-3 p-3 shadow text-center">
				<b>Sisa Cuti Tahunan</b>
				<hr class="mb--2 mt-2">
				<div class="mt-3 row">
					<div class="col-12" v-if="isLoaded == false">
						<div class="pt-2 pb-1 text-center">
							<span class="line" style="width:25px;height:25px;" v-if="isLoaded == false"></span>
						</div>
					</div>
					<div class="col-12" v-if="isLoaded && i == 0" v-for="(val,key,i) in jumCuti">
						<div class="pt-2 pb-1 text-center">
							<div class="mb--1 h2">{{val}}</div>
							<small>Hari</small>
						</div>
					</div>
				</div>
			</div>
			<div class="card text-center">
				<div class="card-body">
					<router-link to="/cuti-user/ajukan-cuti" href="#" class="btn btn-primary btn-block">Ajukan Cuti</router-link>
				</div>
			</div>
			<!-- <div class="card mt-4 shadow">
				<div class="card-body">
					<p class="small text-center">
						<b>Ayat Al - Quran Acak</b>
					</p>
					<p style="font-size:20pt;" class="text-right">
						{{surat.acak.ar.teks}}
					</p>
					<p class="small">Artinya : </p>
					<p class="small">
						{{surat.acak.id.teks}}
					</p>
					<p class="small">
						<b>(Q.S. {{surat.surat.nama}} ({{surat.surat.asma}}) - ayat {{surat.acak.id.ayat}})</b>
					</p>
				</div>
			</div> -->
		</div>
	</div>
</template>

<script>
import axios from 'axios';

export default{
	data(){
		return{
			jumCuti: '',
			tipeCuti: '',
			jumlah: '',
			username: "",
			isLoaded: false,
			divisi: '',
			selamat:'',
			surat:'',
		}
	},
	methods:{
		getDate: function () {
			var greeting;
			var time = new Date().getHours();
			if (time < 10) {
				greeting = "Selamat Pagi";
			} else if (time < 14) {
				greeting = "Selamat Siang";
			} else if(time < 19){
				greeting = "Selamat Sore";
			}else {
				greeting = "Selamat Malam";
			}
			this.selamat = greeting;
			console.log(time);

		},
	},
	mounted(){
		this.getDate();
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};
		var configQuran = {
			headers: {
				
			},
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/home';
		}
		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/home';
		}
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true;
				console.log(response.data);
				this.username = response.data.data[0].name
				this.divisi = response.data.data[0].divisi
				this.jumCuti = response.data.jumlah;
				this.tipeCuti = response.data.tipe;
				this.jumlah = response.data.jumlah;
			}).catch((error) => {
				console.log(error)
			});

			// fetch('')
			// .then(response => response.json())
			// .then(json => console.log(json))
			// fetch(' https://api.banghasan.com/quran/format/json/acak')
			// .then((response) => response.json())
			// .then((messages) => {
			// 	console.log(messages);
			// 	this.surat = messages;
			// });
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>