import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import swal from "sweetalert2";

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		status: '',
		token: localStorage.getItem('token') || '',
		user : [],
		userAccess : localStorage.getItem('access') == "user" ? true : false,
		adminAccess : localStorage.getItem('access') == "admin" ? true : false,
	},
	mutations: {
		auth_request(state){
			state.status = 'loading'
		},
		auth_success(state, token, user){
			state.status = 'success'
			state.token = token
			state.user = user
		},
		auth_error(state){
			state.status = 'error'
		},
		logout(state){
			state.status = ''
			state.token = ''
		},
	},
	actions: {
		login({commit}, user){
			return new Promise((resolve, reject) => {
				commit('auth_request')
				axios({url: 'http://localhost:8000/api/login', data: user, method: 'POST' })
				.then(resp => {
					const token = resp.data.token
					const user = resp.data.user
					const access = resp.data.user.role
					localStorage.setItem('token', token)
					localStorage.setItem('access', access)
					if(access == "user"){
						location.href = '/user'
					}
					else if(access == "admin"){
						location.href = '/admin'
					}
					axios.defaults.headers.common['Authorization'] = token
					commit('auth_success', token, user)
					resolve(resp)
				})
				.catch(err => {
					commit('auth_error')
					localStorage.removeItem('token')
					localStorage.removeItem('access')
					reject(err)
					swal.fire({
						type: 'error',
						title: 'Login Gagal',
						text: 'Username / Password tidak valid',
					}).then(function(){
						console.log("FAIL")
					});
				})
			})
		},
		logout({commit}){
			return new Promise((resolve, reject) => {
				commit('logout')
				localStorage.removeItem('token')
				localStorage.removeItem('access')
				delete axios.defaults.headers.common['Authorization']
				resolve()
			})
		}
	},
	getters : {
		isLoggedIn: state => state.token,
		authStatus: state => state.status,
		userAuth: state => state.userAccess,
		adminAuth: state => state.adminAccess,
	}
})