<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/pengaturan" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Kebijakan Cuti</b>
			</div>
		</div>
		<div class="container mt-6">
			<div class="card mt-3 shadow">
				<div class="card-header text-center">
					<b>Kebijakan Cuti</b>
				</div>
				<div class="card-body p-4">
					<p v-if="!isEditing">
						<span v-html="kebijakanCuti"></span>
						<span class="line text-center mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
						<span class="line text-center mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
						<span class="line text-center mt-3 mb-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
					</p>
					<form @submit.prevent="updateCuti" :style="!isEditing ? 'display:none;' : '' ">
						<textarea id="froala-editor">
							{{kebijakanCuti}}
						</textarea>

						<div class="row">
							<div class="col-6">
								<button type="submit" class="btn btn-primary btn-block mt-2">Ubah</button>
							</div>
							<div class="col-6">
								<a href="javascript:void(0)" class="btn btn-secondary btn-block mt-2" v-on:click="batalEdit">Batal</a>
							</div>
						</div>
					</form>
				</div>
				<div class="card-footer" v-if="!isEditing">
					<button class="btn btn-block btn-primary" v-on:click="editKebijakan">Ubah Kebijakan Cuti</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			kebijakanCuti: '',
			quil:'',
			isEditing:false,

		}
	},
	methods:{
		updateCuti(){
			var isEditing = this.isEditing;

			var val = $("#froala-editor").val();
			var startRequest = this.startReq;
			startRequest = true;
			if(startRequest){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()

					}
				})
			}
			var bodyTambah = {
				kebijakan_cuti: val
			} 
			var token = localStorage.getItem('token');
			var dataArray = this.data;

			axios({url: 'http://localhost:8000/api/admin/kebijakan-cuti/edit', headers:{Authorization: "Bearer " + token},data: bodyTambah, method: 'POST' })
			.then(resp => {
				console.log(resp);
				this.divisi = "";
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Kebijakan berhasil diperbaharui!",
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
			})
		},
		editKebijakan(){
			this.isEditing = true;
			if(this.isEditing){
				new FroalaEditor('textarea#froala-editor')
			}
		},
		batalEdit(){
			this.isEditing = false;
			if(this.isEditing){
				new FroalaEditor('textarea#froala-editor')
			}
		}
	},
	mounted(){

		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/kebijakan-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.kebijakanCuti = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	#editor-container {
		height: 120px;
	}


	</style>