<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-admin" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Konfirmasi Cuti</b>
			</div>
		</div>
		<div class="container mt-6 mb-3 w-100">
			<div class="card" v-if="isLoaded == false">
				<div class="card-body row">
					<div class="col-3">
						<span class="line" style="width:60px; height:60px;" v-if="isLoaded == false"></span>
					</div>
					<div class="col-9">
						<span class="line" style="width:100%;" v-if="isLoaded == false"></span>
						<span class="line" style="width:50%;" v-if="isLoaded == false"></span>
					</div>
					<span class="line mt-3" style="width:100%;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:100%;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:100%;" v-if="isLoaded == false"></span>

					<div class="col-6">
						<span class="line mt-5" style="width:100%;" v-if="isLoaded == false"></span>
					</div>
					<div class="col-6">
						<span class="line mt-5" style="width:100%;" v-if="isLoaded == false"></span>
					</div>

				</div>
			</div>
			<div v-if="dataCuti == '' && isLoaded" class="d-flex justify-content-center align-items-center flex-column" style="height:78vh">
				<img src="/assets/img/empty.svg" style="width:80%">
				<div class="mt-3 text-center">
					<big>Untuk saat ini belum ada data pengajuan cuti</big>
				</div>
			</div>
			<div class="card mt-3 shadow" v-for="d in dataCuti">
				<div class="card-body row">
					<div class="col-3">
						<i class="ni ni-circle-08" style="font-size:35pt;"></i>
					</div>
					<div class="col-9">
						<div class="mt--1 small">{{ d.nama_divisi }}</div>
						<div class="h3">{{ d.name }}</div>
						<div class="small">{{d.email}}</div>
					</div>
					<div class="mt-3 col-12">
						<div class="table-responsive">
							<!-- Projects table -->
							<table class="table">
								<tbody>
									<tr>
										<th scope="row">
											Tanggal Cuti
										</th>
										<td>
											{{ d.mulai_cuti }} <span v-if="d.selesai_cuti != null">-</span> 
											{{ d.selesai_cuti }}
										</td>
									</tr>
									<tr>
										<th scope="row">
											Tipe Cuti
										</th>
										<td>
											{{ d.tipe_cuti }}
										</td>
									</tr>
									<tr>
										<th scope="row">
											Deskripsi
										</th>
										<td>
											{{ d.deskripsi }}
										</td>
									</tr>
									<tr>
										<th scope="row">
											Lama Cuti
										</th>
										<td>
											{{ d.lama_cuti }} Hari
										</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
				<div class="card-footer d-flex justify-content-between">
					<a href="#" class="btn btn-primary w-50" v-on:click="confirmAcc(d.daftar_id,d.user_id,d.name,d.tipe_cuti,d.nama_divisi,d.mulai_cuti,d.selesai_cuti,d.lama_cuti,d.deskripsi,d.email)">Setujui</a>
					<a href="#" class="btn btn-secondary w-50" v-on:click="confirmDiss(d.daftar_id,d.user_id,d.name,d.tipe_cuti,d.nama_divisi,d.mulai_cuti,d.selesai_cuti,d.lama_cuti,d.deskripsi,d.id_cuti,d.email)">Tolak</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";

export default{
	data(){
		return{
			dataCuti: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,

		}
	},
	methods:{
		// daftar_id,user_id,name,tipe_cuti,lama_cuti,nama_divisi,mulai_cuti,selesai_cuti,lama_cuti,deskripsi
		confirmAcc(id,id_user,name,tipeCuti,divisi,mulaiCuti,selesaiCuti,totalCuti,deskripsi,email){
			swal.fire({
				type:"question",
				title: "Anda yakin",
				text: "akan menyetujui cuti "+name+" ?",
				showCloseButton: true,
				showCancelButton: true,
				confirmButtonText:'Saya Yakin',
				cancelButtonText: 'Batal',
				preConfirm:false,
				showLoaderOnConfirm: true,
			}).then(function(status){

				if(status.value){
					swal.fire({
						title: 'Memproses',
						html: 'Tunggu Sebentar ...',
						allowOutsideClick: false,
						onBeforeOpen: () => {
							swal.showLoading()

						}
					})
					var token = localStorage.getItem('token');

					var config = {
						headers: {'Authorization': "bearer " + token}
					};

					if(localStorage.getItem('access') == "admin"){
						var respLink = 'http://localhost:8000/api/admin/confirm/'+id;
					}
					axios.get( 
						respLink,
						config
						).then((response) => {
							console.log(response.data)
							var bodyreqemail = {
								id_user: id_user,
								name: name,
								tipeCuti: tipeCuti,
								divisi: divisi,
								mulaiCuti: mulaiCuti,
								selesaiCuti: selesaiCuti,
								totalCuti: totalCuti,
								deskripsi: deskripsi,
								email: email,
							};
							
							console.log("id_user "+id_user);
							console.log("name "+name);
							console.log("tipeCuti "+tipeCuti);
							console.log("divisi "+divisi);
							console.log("mulaiCuti "+mulaiCuti);
							console.log("selesaiCuti "+selesaiCuti);
							console.log("totalCuti "+totalCuti);
							console.log("deskripsi "+deskripsi);

							axios({url: 'http://localhost:8000/api/admin/emailacc', headers:{Authorization: "Bearer " + token},data: bodyreqemail, method: 'POST' })
							.then(resp => {
								console.log(resp);
								swal.fire({
									type:"success",
									title: "Sukses!",
									text: "Cuti "+name+" berhasil disetujui",
								}).then(function(status){
									if(status.value){
										location.reload();
									}
								})
							})
						}).catch((error) => {
							console.log(error)
						});
					}
				});
		},
		confirmDiss(id,idUser,name,tipeCuti,divisi,mulaiCuti,selesaiCuti,lamaCuti,deskripsi,idCuti,email){
			swal.fire({
				text:"Masukkan alasan ditolak",
				input: 'text',
				type:'question',
				inputAttributes: {
					autocapitalize: 'off'
				},
				showCloseButton: true,
				showCancelButton: true,
				confirmButtonText:'Kirim',
				cancelButtonText: 'Batal',
				showLoaderOnConfirm: true,
				inputValidator: (value) => {
					if (!value) {
						return 'Alasan tidak boleh kosong'
					}
				}
			}).then(function(val){
				if(val.value){

					swal.fire({
						title: 'Memproses',
						html: 'Tunggu Sebentar ...',
						allowOutsideClick: false,
						onBeforeOpen: () => {
							swal.showLoading()

						}
					})

					console.log(val.value);
					var token = localStorage.getItem('token');

					var config = {
						headers: {'Authorization': "bearer " + token},
					};
					console.log("idUser "+idUser);
					console.log("idCuti "+idCuti);
					console.log("lamaCuti "+lamaCuti);
					var bodyData = {
						id: id,
						alasan_ditolak: val.value,
					}
					var bodyTambah = {
						id_user: idUser,
						id_cuti: idCuti,
						lama_cuti: lamaCuti,
						tipeCuti: tipeCuti,
					};
					var bodyTolak = {
						id_user: idUser,
						name: name,
						tipeCuti: tipeCuti,
						divisi: divisi,
						mulaiCuti: mulaiCuti,
						selesaiCuti: selesaiCuti,
						totalCuti: lamaCuti,
						deskripsi: deskripsi,
						alasan: val.value,
						email: email,
					};

					if(localStorage.getItem('access') == "admin"){
						var respLink = 'http://localhost:8000/api/admin/decline/'+id;
					}
					axios({	url: 'http://localhost:8000/api/admin/decline', 
						headers:{Authorization: "Bearer " + token},
						data: bodyData, 
						method: 'POST' })
					.then(resp => {
						console.log("Confirm OK");
						axios({	url: 'http://localhost:8000/api/admin/tambah-jumlah',
							headers:{Authorization: "Bearer"+token},
							data:bodyTambah,
							method: 'POST'})
						.then(resp => {
							console.log("Tambah OK");
							axios({url: 'http://localhost:8000/api/admin/emaildec', headers:{Authorization: "Bearer " + token},data: bodyTolak, method: 'POST' })
							.then(resp => {
								console.log(resp);
								swal.fire({
									type:"success",
									title: "Sukses!",
									text: "Cuti "+name+" berhasil ditolak",
								}).then(function(status){
									if(status.value){
										location.reload();
									}
								})
							})
						});
					})
				}

			})

			
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/pending-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.dataCuti = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>