<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/daftar-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Tambah User</b>
			</div>
		</div>
		<div class="container mt-6">
			<router-link to="/daftar-user/tambah-divisi" class="btn btn-primary btn-block mb-4"> <i class="fa fa-plus mr-1"></i> Tambah Divisi / Departemen</router-link>
			<hr>
			<form @submit.prevent="tambahUser">
				<div class="card bg-gradient-primary ">
					<div class="card-body">
						<div class="text-center">
							<b class="text-center text-white">Tambah User</b>
						</div>
						<hr class="mt-3">
						<div class="input-group input-group-alternative mt-3 mb-3">
							<div class="input-group-prepend">
								<span class="input-group-text"><i class="ni ni-paper-diploma"></i></span>
							</div>
							<input type="text" class="form-control" v-model="nik" required placeholder="NIK"/>
						</div>
						<div class="input-group input-group-alternative mt-3">
							<div class="input-group-prepend">
								<span class="input-group-text"><i class="ni ni-badge"></i></span>
							</div>
							<select required class="form-control shadow" v-model="divisi">
								<option selected disabled value="">Divisi / Departemen</option>
								<option v-for="data in dataTipe" :value="data.id" style="width:80%;">{{data.nama_divisi}}</option>
							</select>
						</div>
						<div class="input-group input-group-alternative mt-3 mb-3">
							<div class="input-group-prepend">
								<span class="input-group-text"><i class="ni ni-circle-08"></i></span>
							</div>
							<input type="text" class="form-control" v-model="nama" required placeholder="Nama Lengkap"/>
						</div>
						<div class="input-group input-group-alternative mt-3 mb-3">
							<div class="input-group-prepend">
								<span class="input-group-text"><i class="ni ni-email-83"></i></span>
							</div>
							<input type="email" class="form-control" v-model="email" required placeholder="Email"/>
						</div>
						<div class="input-group input-group-alternative mt-3 mb-3">
							<div class="input-group-prepend">
								<span class="input-group-text">
									<i class="ni ni-key-25"></i>
								</span>
							</div>
							<input :type="showPassword ? 'text' : 'password'" class="form-control bg-white" v-model="password" required placeholder="Password"/>
							<div class="input-group-append">
								<a href="javascript:void(0)" v-if="!showPassword" class="btn bg-white" v-on:click="showPw()"><i class="fa fa-eye"></i></a>
								<a href="javascript:void(0)" v-if="showPassword" class="btn bg-white" v-on:click="hidePw()"><i class="fa fa-eye-slash"></i></a>
							</div>
							<div class="input-group-append">
								<a href="javascript:void(0)" v-on:click="generatePassword()" class="btn btn-primary"><i class="fa fa-refresh"></i></a>
							</div>
						</div>
						<div class="input-group input-group-alternative mt-3">
							<div class="input-group-prepend">
								<span class="input-group-text"><i class="fa fa-users"></i></span>
							</div>
							<select required class="form-control shadow" v-model="hakAkses">
								<option value="user" selected>User</option>
								<option value="admin">Admin</option>
							</select>
						</div>
						<button type="submit" class="btn btn-primary btn-block mt-3">Tambah</button>
					</div>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			dataTipe: '',

			nik:'',
			divisi: '',
			nama: '',
			email: '',
			password: '',
			passwordConfirm: '',
			alamat: '',
			hakAkses: 'user',
			password: '',
			showPassword: false,

		}
	},
	methods:{
		showPw(){
			this.showPassword = true
		},
		hidePw(){
			this.showPassword = false
		},
		makeid(length) {
			var result           = '';
			var characters       = 'abcdefghijklmnopqrstuvwxyz0123456789';
			var charactersLength = characters.length;
			for ( var i = 0; i < length; i++ ) {
				result += characters.charAt(Math.floor(Math.random() * charactersLength));
			}
			return result;
		},
		generatePassword(){
			var passwordRandom = this.makeid(8);
			this.password = passwordRandom;
		},
		tambahUser(){
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()

					}
				})
			}
			var token = localStorage.getItem('token');

			var config = {
				headers: {'Authorization': "bearer " + token},
			}
			var bodyTambah = {
				nik: this.nik, 
				role: this.hakAkses, 
				id_divisi: this.divisi, 
				name: this.nama, 
				email: this.email, 
				password: this.password, 
				password_confirmation: this.password, 
			} 
			console.log(bodyTambah);

			axios({url: 'http://localhost:8000/api/admin/register', headers:{Authorization: "Bearer " + token},data: bodyTambah, method: 'POST' })
			.then(resp => {
				console.log(resp);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "User berhasil dibuat!",
				}).then(function(status){
					if(status.value){
						window.location.reload();
					}
				})
			})
		},
		
	},
	mounted(){
		var passwordRandom = this.makeid(8);
		this.password = passwordRandom;

		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/profile';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.dataTipe = response.data.tipe
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>