<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/daftar-user/tambah-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Tambah Divisi</b>
			</div>
		</div>
		<div class="container mt-6">
			<form @submit.prevent="tambahDivisi" class="mt-3">
				<div class="input-group input-group-merge shadow">
					<input class="form-control bg-white" placeholder="Divisi" type="text" style="border-right:1px solid #eaeaea;" v-model="divisi" required>
					<div class="input-group-append">
						<button type="submit" class="btn btn-primary"><i class="fa fa-plus"></i></button>
					</div>
				</div>
			</form>
			<span class="line mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<ul class="list-group">
				<li class="list-group-item px-0 shadow mt-3" v-for="d,i in data">
					<div class="row align-items-center">
						<div class="col-auto">
						</div>
						<div class="col ml--2">
							<h4 class="mb-0 text-muted">
								{{d.nama_divisi}}
							</h4>
						</div>
						<div class="col-auto mr-3">
							<button type="button" class="btn btn-sm btn-primary" v-on:click="hapusDivisi(i,d.id,d.nama_divisi)"><i class="fa fa-trash"></i></button>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			isLoaded: false,
			startReq: false,
			startReq2: false,
			data: '',
			divisi: '',
		}
	},
	methods:{
		tambahDivisi(){
			var startRequest = this.startReq;
			startRequest = true;
			if(startRequest){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()

					}
				})
			}
			var bodyTambah = {
				nama_divisi: this.divisi
			} 
			console.log(bodyTambah);
			var token = localStorage.getItem('token');
			var dataArray = this.data;

			axios({url: 'http://localhost:8000/api/admin/tambah-divisi', headers:{Authorization: "Bearer " + token},data: bodyTambah, method: 'POST' })
			.then(resp => {
				this.divisi = "";
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Divisi / Departemen berhasil ditambah!",
				}).then(function(status){
					if(status.value){
						dataArray.push(resp.data);
						startRequest = false;
					}
				})
			})
		},
		hapusDivisi(index,id,namaDivisi){
			var arrayData = this.data;
			var startRequest = this.startReq;
			Swal.fire({
				title: 'Konfirmasi',
				type: 'question',
				html:
				'Anda yakin akan menghapus divisi ini?',
				showCancelButton: true,
				focusConfirm: false,
				confirmButtonText:"Hapus",
				cancelButtonText:'Batal',
			}).then(function(result) {
				if(result.value){
					startRequest = true;
					if(startRequest){
						Swal.fire({
							title: 'Memproses',
							html: 'Tunggu Sebentar ...',
							allowOutsideClick: false,
							onBeforeOpen: () => {
								Swal.showLoading()

							}
						})
					}
					var token = localStorage.getItem('token');

					var config = {
						headers: {'Authorization': "bearer " + token}
					};

					axios.get( 
						'http://localhost:8000/api/admin/hapus-divisi/'+id,
						config
						).then((response) => {
							Swal.fire({
								type:"success",
								title: "Sukses!",
								text: "Divisi / Dpeartemen berhasil dihapus!",
							}).then(function(status){
								if(status.value){
									arrayData.splice(index,1);
									startRequest = false;
								}
							})
						}).catch((error) => {
							console.log(error)
						});
					}
				});
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/get-divisi';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true;
				this.data = response.data;
			}).catch((error) => {
				console.log(error)
			});
		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>