<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/pengaturan" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Email Aktif Admin</b>
			</div>
		</div>
		<div class="container mt-6">
			<div class="card shadow text-center">
				<div class="card-body">
					<q>Email ini digunakan sebagai email utama dalam penerimaan pengajuan cuti oleh karyawan</q>
				</div>
			</div>
			<div class="card mt-3 shadow text-center">
				<div class="card-header">
					<b>Email</b>
				</div>
				<div class="card-body">
					<span v-if="!isEditing">{{email}}</span>
					<span class="line" style="width:105px;" v-if="isLoaded == false"></span>
					<form v-if="isEditing" @submit.prevent="updateEmail">
						<input type="email" name="" v-model="email" class="form-control" required>
						<div class="row mt-3" v-if="isEditing">
							<div class="col-6">
								<button type="submit" class="btn btn-primary btn-block">Perbaharui</button>
							</div>
							<div class="col-6">
								<button class="btn btn-secondary btn-block" v-on:click="cancelEdit">Batal</button>
							</div>
						</div>
					</form>
				</div>
				<div class="card-footer">
					<button class="btn btn-block btn-primary" v-on:click="editing" v-if="!isEditing">Ubah</button>
					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			email: '',
			isEditing: false,
			emailNormal:'',

		}
	},
	methods:{
		editing(){
			this.isEditing = true
		},
		cancelEdit(){
			this.isEditing = false
			this.email = this.emailNormal
		},
		updateEmail(){
			this.isEditing = false
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()
					}
				})
			}
			var token = localStorage.getItem('token')
			var bodyReq = {
				email: this.email
			};
			axios({url: 'http://localhost:8000/api/admin/email-hr/edit', headers:{Authorization: "Bearer " + token},data: bodyReq, method: 'POST' })
			.then(resp => {
				console.log(resp);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Data berhasil diperbaharui!",
					allowOutsideClick: false,
				}).then(function(status){
					if(status.value){
						// location.reload();
					}
				})
			}).catch(error => {
				console.log(error)
				this.startReq = false
			});
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/email-hr';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.email = response.data
					this.emailNormal = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>