<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary pb-4">
			<div class="text-center p-4 text-white">

			</div>
		</div>
		<div class="col-xl-4 order-xl-2 mb-5 mb-xl-0 mt-3">
			<div class="card card-profile shadow">
				<div class="row justify-content-center">
					<div class="col-lg-3 order-lg-2">
						<div class="card-profile-image">
							<img src="/assets/img/avatar.png" class="rounded-circle" style="border:5px solid #f7736c;">
						</div>
					</div>
				</div>
				<div class="text-center">
					<div class="mt-9" v-if="!isLoaded"></div>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3 mb-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
				</div>
				<div class="card-body pt-0 pt-md-4" v-if="isLoaded == true">
					<div class="text-center mt-9"></div>
					<form @submit.prevent="ubahProfile">
						<div class="text-center mt-4">
							<h3>
								<span v-if="!editing">{{user.name}}<span class="font-weight-light"></span></span>
								<input type="text" required placeholder="name" v-model="user.name" class="form-control" v-if="editing">
							</h3>
						</div>
						<hr class="mt-3 mb-3">
						<div class="text-center">
							<span v-if="!editing">Tanggal Lahir {{user.tanggal_lahir}}</span>
							<label  v-if="editing">Tanggal Lahir</label>
							<input type="date" placeholder="tanggal_lahir" v-model="user.tanggal_lahir" class="form-control border-primary" v-if="editing">
						</div>
						<hr>
						<div class="text-center">

							<div class="h5 font-weight-300">
								<span v-if="!editing"><i class="ni location_pin mr-2"></i>{{user.alamat}}</span>
								<input type="text" required placeholder="alamat" v-model="user.alamat" class="form-control border-primary" v-if="editing">
							</div>
							<div>
								<span v-if="!editing"><i class="ni education_hat mr-2"></i>NIK : {{user.nik}}</span>
							</div>
							<hr class="my-4">
							<span v-if="!editing"><a href="#">{{user.email}}</a></span>
							<hr>
						</div>
						<div class="text-center d-flex justify-content-between">
							<a href="javascript:void(0)" class="btn btn-block btn-primary" v-on:click="editProfile()" v-if="!editing">Ubah Profil</a>
							<button  type="submit" class="btn w-50 btn-primary" v-if="editing">Ubah</button>
							<router-link to="/profile" class="btn w-50 btn-secondary" v-if="editing">Batal</router-link>
						</div>
						<hr>
						<a href="javascript:void(0)" class="btn btn-block btn-primary mt-3" v-on:click="ubahSandi()">Ubah Sandi</a>
					</form>
					
				</div>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			user: '',
			isLoaded: false,
			editing:true,
			tipeDivisi: '',
			startReq: false,
		}
	},
	methods:{
		ubahSandi(){
			Swal.fire({
				text:"Masukan Password Baru",
				input: 'password',
				type:'question',
				inputAttributes: {
					autocapitalize: 'off'
				},
				showCloseButton: true,
				showCancelButton: true,
				confirmButtonText:'Kirim',
				cancelButtonText: 'Batal',
				showLoaderOnConfirm: true,
				inputValidator: (value) => {
					if (!value) {
						return 'Password tidak boleh kosong'
					}
					else if (value.length < 8) {
						return 'Password tidak boleh kurang dari 8 karakter'
					}
				}
			}).then(function(result){
				if(result.value){
					Swal.fire({
						title: 'Memproses',
						html: 'Tunggu Sebentar ...',
						allowOutsideClick: false,
						onBeforeOpen: () => {
							Swal.showLoading()
						}
					})
					var token = localStorage.getItem('token')
					var bodyReq = {
						password: result.value
					};
					if(localStorage.getItem('access') == "admin"){
						var respLink = 'http://localhost:8000/api/admin/ubah-sandi';
					}
					if(localStorage.getItem('access') == "user"){
						var respLink = 'http://localhost:8000/api/user/ubah-sandi';
					}
					axios({url: respLink, headers:{Authorization: "Bearer " + token,},
						data: bodyReq, method: 'POST' })
					.then(resp => {
						console.log(resp);
						Swal.fire({
							type:"success",
							title: "Sukses!",
							text: "Sandi berhasil diperbaharui!",
							allowOutsideClick: false,
						}).then(function(status){
							if(status.value){
								location.reload();
							}
						})
					})
				}
			})
		},
		editProfile(){
			this.editing = true
			console.log(this.editing)
		},
		batalEditProfile(){
			this.editing = false
			console.log(this.editing)
		},
		ubahProfile(){
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()
					}
				})
			}
			var token = localStorage.getItem('token')
			console.log(this.user);
			var bodyReq = this.user;
			axios({url: 'http://localhost:8000/api/user/profile/edit', headers:{
				Authorization: "Bearer " + token,
				'Content-Type': "application/json",
			},data: bodyReq, method: 'POST' })
			.then(resp => {
				console.log(resp);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Data berhasil diperbaharui!",
					allowOutsideClick: false,
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
			}).catch(error => {
				console.log(error.response.data.errors)
				Swal.fire({
					type:"error",
					title: "Gagal!",
					text: error.response.data.errors.email,
					allowOutsideClick: false,
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
				this.startReq = false
			});
		},
	},
	mounted(){
		
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/profile';
		}
		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/profile';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				if(response.status == 200){
					this.user = response.data.cutiUser[0]
					this.tipeDivisi = response.data.tipe;
					console.log(response.data);
				}
			}).catch((error) => {
				console.log(error)
			});

		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		float:right;
		font-size:20pt;
		margin-top:7px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>