<template>
  <div class="main-content  bg-gradient-primary" style="height:100vh;">
    <!-- Header -->
    <div class="header py-7 py-lg-8 bg-white">
      <div class="text-center text-muted mt--5">
        <img src="assets/img/brand/blue.png" alt="logo" style="width:200px;">
      </div>
    </div>
    <!-- Page content -->
    <div class="container mt--6 pb-5">
      <!-- Table -->
      <div class="row justify-content-center">
        <div class="col-lg-6 col-md-8">
          <div class="card bg-secondary shadow border-1">
            <div class="card-body px-lg-5 py-lg-5">
              <form @submit.prevent="login">
                <div class="form-group mt-4">
                  <div class="input-group input-group-alternative mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text"><i class="ni ni-email-83"></i></span>
                    </div>
                    <input class="form-control" v-model="email" placeholder="Email" type="email" required>
                  </div>
                </div>
                <div class="form-group mt-4">
                  <div class="input-group input-group-alternative mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text"><i class="ni ni-key-25"></i></span>
                    </div>
                    <input class="form-control" v-model="password" placeholder="Password" type="password" required>
                  </div>
                </div>
                <div class="text-center mt-5">
                  <button type="submit" class="btn btn-primary btn-block mt-4">Login</button>
                </div>
                <div class="text-center text-muted mt-4">
                  <div>
                    <div class="spinner-grow text-primary mt-3" v-if="startReq"></div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  data(){
    return {
      email : "",
      password : "",
      startReq: false,
    }
  },

  methods: {
    login: function () {
      this.startReq = true;
      let email = this.email 
      let password = this.password
      this.$store.dispatch('login', { email, password })
      .catch(err => this.startReq = false)
    }
  }
}
</script>

<style scoped>
.loadingfull{
  position:fixed;
  background: rgba(0,0,0,0.9);
  top:0;
  left:0;
  width: 100vw;
  height: 100vh;
}
.loadingfull div{
  position:absolute;
  top:45%;
  left:45%;
}
</style>