<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-admin" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Tipe Cuti</b>
			</div>
		</div>
		<div class="container mt-6">
			<form @submit.prevent="tambahTipe">
				<div class="input-group input-group-merge shadow">
					<input class="form-control bg-white" placeholder="Tipe Cuti" type="text" style="border-right:1px solid #eaeaea;" v-model="tipeCutiInput" required>
					<div class="input-group-append">
						<input class="form-control bg-white" style="width:70px;border-radius:0px !important;" placeholder="Lama" type="number" width="10px" v-model="lamaCutiInput" required>
					</div>
					<div class="input-group-append">
						<button type="submit" class="btn btn-primary"><i class="fa fa-plus"></i></button>
					</div>
				</div>
			</form>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<ul class="list-group">
				<li class="list-group-item px-0 shadow mt-3" v-for="tipe in dataCuti">
					<div class="row align-items-center">
						<div class="col-auto">
						</div>
						<div class="col ml--2">
							<h4 class="mb-0 text-primary">
								{{tipe.tipe_cuti}}
							</h4>
							<small>{{tipe.lama_cuti}} Hari</small>
						</div>
						<div class="col-auto mr-3">
							<button type="button" class="btn btn-sm btn-primary" v-on:click="hapusTipe(tipe.id,tipe.tipe_cuti)"><i class="fa fa-trash"></i></button>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			dataCuti: '',

		}
	},
	methods:{
		replaceSpasi(val){
			return val.replace(/ /g, '_');
		},
		tambahTipe(e){
			var token = localStorage.getItem('token');
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()

					}
				})
			}
			console.log(this.tipeCutiInput);
			console.log(this.lamaCutiInput);
			var tipeCutiInput = this.replaceSpasi(this.tipeCutiInput)
			var bodyTambah = {
				tipe_cuti: tipeCutiInput,
				tipe_cuti_normal: this.tipeCutiInput,
				lama_cuti: this.lamaCutiInput,
			}
			this.tipeCutiInput = "";
			this.lamaCutiInput = "";

			e.preventDefault();
			console.log(bodyTambah);
			axios({url: 'http://localhost:8000/api/admin/buat-tipe-cuti', headers:{Authorization: "Bearer " + token},data: bodyTambah, method: 'POST' })
			.then(resp => {
				console.log(resp);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Tipe cuti berhasil ditambahkan!",
				}).then(function(status){
					if(status.value){
						location.reload();
						// this.dataCuti.push([bodyTambah]);
					}
				})
			}).catch(error => {
				Swal.fire({
					type:"error",
					title: "Gagal",
					text: "Tipe cuti tersebut sudah ada!",
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
			});
		},
		hapusTipe(id,name){
			var token = localStorage.getItem('token');
			var config = {
				headers: {'Authorization': "bearer " + token}
			};
			Swal.fire({
				title: 'Konfirmasi',
				type: 'question',
				html:
				'Anda yakin akan menghapus tipe cuti ini?',
				showCancelButton: true,
				focusConfirm: false,
				confirmButtonText:"Hapus",
				cancelButtonText:'Batal',
			}).then((result) => {
				if(result.value){
					this.startReq = true;
					if(this.startReq){
						Swal.fire({
							title: 'Memproses',
							html: 'Tunggu Sebentar ...',
							allowOutsideClick: false,
							onBeforeOpen: () => {
								Swal.showLoading()

							}
						})
					}
					axios.get( 
						'http://localhost:8000/api/admin/hapus-tipe-cuti/'+id+'/'+name,
						config,
						).then((response) => {
							console.log(response.data)
							this.isLoaded = true
							if(response.status == 200){
								Swal.fire({
									type:"success",
									title: "Sukses!",
									text: "Tipe cuti berhasil dihapus!",
								}).then(function(status){
									if(status.value){
										location.reload();
									}
								})
							}
						}).catch((error) => {
							Swal.fire({
								type:"error",
								title: "tidak dapat menghapus tipe!",
								text: "Tipe cuti ini masih terdapat pada data!",
							}).then(function(status){
								if(status.value){
									location.reload();
								}
							})
						});
					}
				});
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/get-tipe-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.dataCuti = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>