<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-admin" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Ajukan Cuti</b>
			</div>
		</div>
		<div class="container mt-6 mb-3 w-100">
			<div class="card mt-3 p-3 shadow text-center">
				<b>Sisa Cuti Tahunan</b>
				<hr class="mb--2 mt-2">
				<div class="mt-3 row">
					<div class="col-12" v-if="isLoaded == false">
						<div class="pt-2 pb-1 text-center">
							<span class="line" style="width:25px;height:25px;" v-if="isLoaded == false"></span>
						</div>
					</div>
					<div class="col-12" v-if="isLoaded && i == 0" v-for="(val,key,i) in jumCuti">
						<div class="pt-2 pb-1 text-center">
							<div class="mb--1 h2">{{val}}</div>
							<small>Hari</small>
						</div>
					</div>
				</div>
			</div>
			
			<div class="mt-3">
				<div class="card shadow bg-gradient-primary border-0">
					<form @submit.prevent="ajukanCuti">
						<div class="card-body">
							<div class="form-group mt-3">
								<input type="hidden" name="id" >
								<div class="input-group input-group-alternative mt-3">
									<div class="input-group-prepend">
										<span class="input-group-text"><i class="ni ni-bus-front-12"></i></span>
									</div>
									<select required class="form-control" id="tipeCutiOps" @change="onChange($event)" >
										<option selected disabled value>Pilih Tipe Cuti</option>
										<optgroup label="Cuti Tahunan">
											<option v-for="(val,key,i) in jumCuti" v-if="i == 0" v-bind:value="val">{{key}}</option>
										</optgroup>
										<optgroup label="Cuti Khusus">
											<option v-for="(val,key,i) in jumCuti" v-if="i >= 1" v-bind:value="val">{{key}}</option>
										</optgroup>
									</select>
								</div>
								<div class="input-group input-group-alternative mt-3 mb-3">
									<div class="input-group-prepend">
										<span class="input-group-text"><i class="ni ni-time-alarm"></i></span>
									</div>
									<input type="date" v-model="mulaiCuti" class="form-control" id="startDate" required  />
								</div>
								<div class="input-group input-group-alternative" id="endDateDiv" style="display:none;">
									<div class="input-group-prepend">
										<span class="input-group-text"><i class="ni ni-time-alarm"></i></span>
									</div>
									<input type="date" v-model="selesaiCuti"  class="form-control" id="endDate" @change="onChangeDate($event)"/>
								</div>


								<div class="custom-control custom-control-alternative custom-checkbox mt-3">
									<input class="custom-control-input" id="checkbox1" cek="false" type="checkbox" v-on:click="check()" >
									<label class="custom-control-label" for="checkbox1">
										<small class="text-white mt-3">Lebih dari 1 hari</small>
									</label>
								</div>
								<div class="input-group input-group-alternative mt-3">
									<textarea class="form-control" placeholder="Deskripsi" v-model="deskripsi" rows="3" required ></textarea>
								</div>

								<div class="custom-control custom-control-alternative custom-checkbox mt-3">
									<input class="custom-control-input" id="confirm" type="checkbox" required>
									<label class="custom-control-label text-white mb--2" for="confirm">
										<small>Saya mengambil cuti <strong>{{ pilihTipe }}</strong> selama <strong>{{ totalCuti }} Hari</strong></small>
									</label>
								</div>

								<input type="submit" class="btn btn-primary mt-3 btn-block" value="Ajukan Cuti">
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import Vue from 'vue'
import $ from 'jquery';
import axios from 'axios';
import Swal from "sweetalert2";
import router from 'vue-router'
Vue.use(router);

export default{
	data(){
		return{
			data: '',
			isLoaded: false,
			jumCuti: '',
			isChecked: false,
			totalCuti: 1,
			pilihTipe: '',
			sisaCuti: '',
			deskripsi: '',

			mulaiCuti: '',
			selesaiCuti: '',

			startReq: false,

			habisMengajukan: false,
			id_daftar:'',
		}
	},
	methods:{
		onChange(event) {
			this.sisaCuti = event.target.value;
			this.pilihTipe = $("#tipeCutiOps option:selected").text();
		},
		formatDate (input) {
			var datePart = input.match(/\d+/g),
			year = datePart[0].substring(0),
			month = datePart[1], day = datePart[2];

			return day+'-'+month+'-'+year;
		},
		ajukanCuti(){
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()

					}
				})
			}
			var token = localStorage.getItem('token');

			var config = {
				headers: {'Authorization': "bearer " + token},
			};



			if(this.totalCuti <= this.sisaCuti){
				if(this.selesaiCuti != ''){
					this.selesaiCuti = this.formatDate(this.selesaiCuti)
				}
				else{
					this.selesaiCuti = '';
				}
				var bodyData = {
					id_user: this.data.id,
					tipe_cuti: this.pilihTipe,
					deskripsi: this.deskripsi,
					mulai_cuti: this.formatDate(this.mulaiCuti),
					selesai_cuti: this.selesaiCuti,
					lama_cuti: this.totalCuti,
				}
				var bodyKurangi = {
					id_user: this.data.id,
					tipeCuti: this.pilihTipe,
					ambil_cuti: this.totalCuti,
				}

				axios({url: 'http://localhost:8000/api/admin/ajukan-cuti/process', headers:{Authorization: "Bearer " + token},data: bodyData, method: 'POST' })
				.then(resp => {
					// Kurangi Cuti
					this.id_daftar = resp.data;
					axios({url: 'http://localhost:8000/api/admin/kurangi', headers:{Authorization: "Bearer " + token},data: bodyKurangi, method: 'POST' })
					.then(resp => {
						var bodyreqemail = {
							id_user: this.data.id,
							name: this.data.name,
							tipeCuti: this.pilihTipe,
							totalCuti: this.totalCuti,
							divisi: this.data.nama_divisi,
							mulaiCuti: this.formatDate(this.mulaiCuti),
							selesaiCuti: this.selesaiCuti,
							deskripsi: this.deskripsi,
							id_daftar: this.id_daftar, 
						}
						// console.log("bodyreqemail "+bodyreqemail);
						axios({url: 'http://localhost:8000/api/admin/emailreq', headers:{Authorization: "Bearer " + token},data: bodyreqemail, method: 'POST' })
						.then(resp => {
							console.log(this.data);
							Swal.fire({
								type:"success",
								title: "Sukses!",
								text: "Cuti berhasil diajukan ke HR!",
							}).then(function(status){
								if(status.value){
									window.location.href = 'status-pengajuan';
								}
							})
						})
					})
				})
			}else{
				Swal.fire({
					type: "error",
					title: 'Gagal',
					text: "Sisa cuti Anda tidak mencukupi"
				})
			}
		},
		// ajukanCuti(){
		// 	console.log(this.formatDate(this.selesaiCuti));
		// },
		days_between(date1, date2) {
		    // The number of milliseconds in one day
		    const ONE_DAY = 1000 * 60 * 60 * 24;

		    // Calculate the difference in milliseconds
		    const differenceMs = Math.abs(date1 - date2);

		    // Convert back to days and return
		    return Math.round(differenceMs / ONE_DAY) + 1;

		},
		onChangeDate(event){
			var startDate = new Date($("#startDate").val());
			var endDate = new Date(event.target.value);
			var totalLamaCuti = this.days_between(startDate,endDate);
			this.totalCuti = totalLamaCuti;
		},
		check(){
			if(this.isChecked == false){
				this.isChecked = true;
			}
			else if(this.isChecked){
				this.totalCuti = 1;
				this.isChecked = false;
				this.selesaiCuti = "";
			}
			console.log(this.isChecked);
		}
	},
	mounted(){
		this.selesaiCuti = "";
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/ajukan-cuti';
		}
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				this.jumCuti = response.data.jumlah;
				this.data = response.data.data[0];
				console.log(response.data);
			}).catch((error) => {
				console.log(error)
			});


			var dtToday = new Date();

			var month = dtToday.getMonth() + 1;
			var day = dtToday.getDate();
			var year = dtToday.getFullYear();
			if(month < 10)
				month = '0' + month.toString();
			if(day < 10)
				day = '0' + day.toString();

			var maxDate = year + '-' + month + '-' + day;
			$('#startDate').attr('min', maxDate);
			$('#startDate').val(maxDate);
			$('#endDate').attr('min', maxDate);
			$('#endDate').val() == "";
			var maxDate = new Date($("#startDate").val());
			$('#startDate').change(function() {
				var date = $(this).val();
				$('#endDate').attr('min', date);
			});


			$("#checkbox1").change(function() {
				if(this.checked) {
					$("#endDateDiv").slideDown();
					$('#endDate').attr('required',true);
				}
				else{
					$('#endDate').removeAttr('required',false);
					$("#endDateDiv").slideUp();
					$("#endDate").val("")
					this.selesaiCuti = "";
				}
			});
		}
	}
	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background: #F7736C;
		color: #fff !important;
	}
	</style>