import Vue from 'vue'
import Router from 'vue-router'
import store from './store.js'
import Login from './components/login.vue'
import User from './components/user/home-user.vue'
import Admin from './components/admin/home-admin.vue'
import cutiAdmin from './components/admin/cuti-admin.vue'
import cutiUser from './components/user/cuti-user.vue'
import daftarUser from './components/admin/daftar-user.vue'
import Konfirmasi from './components/admin/konfirmasi-cuti.vue'
import ajukanCuti from './components/user/ajukan-cuti.vue'
import ajukanCutiAdmin from './components/admin/ajukan-cuti.vue'
import Profile from './components/admin/profile.vue'
import Status from './components/user/status-pengajuan.vue'
import TipeCuti from './components/admin/tipe-cuti.vue'
import EditSisa from './components/admin/edit-sisa-cuti.vue'
import EditUser from './components/admin/edit-user.vue'
import Kalkulasi from './components/user/kalkulasi-cuti.vue'
import tambahUser from './components/admin/tambah-user.vue'
import tambahDivisi from './components/admin/tambah-divisi.vue'
import Pengaturan from './components/admin/pengaturan.vue'
import EmailAktif from './components/admin/email-aktif-admin.vue'
import ResetCuti from './components/admin/reset-cuti.vue'
import KebijakanCuti from './components/admin/kebijakan-cuti.vue'
import KebijakanCutiUser from './components/user/kebijakan-cuti.vue'
import StatusCuti from './components/admin/status-cuti.vue'
import SedangCuti from './components/admin/sedang-cuti.vue'
import UbahProfile from './components/admin/ubah-profile.vue'


Vue.use(Router)

let router = new Router({
  mode: 'history',
  routes: [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/user',
    name: 'user',
    component: User,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/profile/edit',
    name: 'UbahProfile',
    component: UbahProfile,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/pengaturan',
    name: 'Pengaturan',
    component: Pengaturan,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/pengaturan/email-aktif-admin',
    name: 'EmailAktif',
    component: EmailAktif,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/pengaturan/kebijakan-cuti',
    name: 'KebijakanCuti',
    component: KebijakanCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/pengaturan/reset-cuti',
    name: 'ResetCuti',
    component: ResetCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-user/status-pengajuan',
    name: 'status',
    component: Status,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-user/kebijakan-cuti',
    name: 'KebijakanCutiUser',
    component: KebijakanCutiUser,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-user/kalkulasi-cuti',
    name: 'kalkulasi',
    component: Kalkulasi,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-admin/konfirmasi-cuti',
    name: 'konfir',
    component: Konfirmasi,
    meta: { 
      requiresAuth: true,
    }
  },
   {
    path: '/cuti-admin/status-cuti',
    name: 'StatusCuti',
    component: StatusCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-admin/tipe-cuti',
    name: 'tipeCuti',
    component: TipeCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-admin',
    name: 'cutiAdmin',
    component: cutiAdmin,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-user',
    name: 'cutiUser',
    component: cutiUser,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/daftar-user',
    name: 'daftarUser',
    component: daftarUser,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/daftar-user/tambah-divisi',
    name: 'tambahDivisi',
    component: tambahDivisi,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/daftar-user/tambah-user',
    name: 'tambahUser',
    component: tambahUser,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/daftar-user/edit-sisa-cuti/:id',
    name: 'EditSisa',
    component: EditSisa,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/daftar-user/edit-user/:id',
    name: 'EditUser',
    component: EditUser,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-user/ajukan-cuti',
    name: 'ajukanCuti',
    component: ajukanCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-admin/ajukan-cuti',
    name: 'ajukanCutiAdmin',
    component: ajukanCutiAdmin,
    meta: { 
      requiresAuth: true,
    }
  },
  {
    path: '/cuti-admin/sedang-cuti',
    name: 'SedangCuti',
    component: SedangCuti,
    meta: { 
      requiresAuth: true,
    }
  },
  ]
})

router.beforeEach((to, from, next) => {
  // if(to.meta.requiresAuth){
  //   next();
  // }
  // else{
  //   next();
  // }

  if(to.path == "/" && store.getters.isLoggedIn){
    if(localStorage.getItem('access') == "admin"){
      router.push("/admin")
    }
    if(localStorage.getItem('access') == "user"){
      router.push("/user")
    }
  }
  if(to.meta.requiresAuth) {
    if (store.getters.isLoggedIn) {
      next()
      return
      if(localStorage.getItem('access') == "admin" && to.meta.userAuth){
        next()
      }
      if(localStorage.getItem('access') == "user" && to.meta.adminAuth){
        next()
      }
    }
    next('/') 
  } else {
    next()
  }

  //  if(to.meta.requiresAuth) {
  //   const access = localStorage.getItem('access');
  //   if(to.meta.adminAuth && access == "admin"){
  //       next()
  //       return
  //   }
  // }
  // else{
  //   next();
  // }
  
})

export default router