<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-admin" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Sedang Cuti</b>
			</div>
		</div>
		<div class="container mt-5 mb-3 w-100">
			<div class="card shadow mt-6">
				<div class="card-header text-center">
					<b>Pilih Tanggal</b>
				</div>
				<div class="card-body">
					<input type="date" name="" class="form-control" v-model="theDate">
					<div class="row">
						<div class="col-6">
							<a href="javascript:void(0)" @click="min" class="btn btn-block mt-3 btn-primary"><i class="fa fa-arrow-left"></i></a>
						</div>
						<div class="col-6">
							<a href="javascript:void(0)" @click="plus" class="btn btn-block mt-3 btn-primary"><i class="fa fa-arrow-right"></i></a>
						</div>
					</div>
				</div>
			</div>
			<span class="line mt-4" style="width:100%; height:120px; border-radius:5px !important;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:120px; border-radius:5px !important;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:120px; border-radius:5px !important;" v-if="isLoaded == false"></span>


			<div class="card bg-white shadow mt-4" v-for="data,i in dataCuti" v-if="isInRange(theDate,ubahFormatNomral(data.mulai_cuti),ubahFormatNomral(data.selesai_cuti)) || ubahFormat(data.mulai_cuti) == theDate">
				<div class="card-body">
					<div class="row">
						<div class="col">
							<h5 class="card-title text-uppercase text-muted mb-0">Cuti</h5>
							<span class="h2 font-weight-bold mb-0">{{data.tipe_cuti}} <span class="small">({{data.status}})</span></span>
						</div>
						<div class="col-auto">
							<div class="p-2 bg-danger text-white rounded-circle shadow" v-if="data.status == 'ditolak'"></div>
							<div class="p-2 bg-success text-white rounded-circle shadow" v-if="data.status == 'disetujui'"></div>
						</div>
					</div>
					<div class="text-nowrap mt-1">{{data.mulai_cuti}} <span v-if="data.selesai_cuti != null">- {{data.selesai_cuti}}</span> ({{data.lama_cuti}} Hari)</div>
					<hr class="mt-4 mb-4">
					<div class="mt-3">
						<div class="text-muted text-uppercase h5">Profil</div>
						<b>{{data.name}} <small>({{data.nama_divisi}})</small></b>
					</div>
					<hr class="mt-4 mb-4">
					<div>
						<div class="text-muted text-uppercase h5">Deskripsi</div>
						{{data.deskripsi}}
					</div>
				</div>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";
import $ from 'jquery';


export default{
	data(){
		return{
			user: '',
			isLoaded: false,
			tipeStatus: 'ditolak',
			// year - (month-1) - date
			theDate: '',
			nowDate: new Date(),
			theDateConvert: '',
			dataCuti: '',
			isAvailable: false,
		}
	},
	methods:{
		ubahFormat(val){
			var date = val;
			var datearray = date.split("-");

			return datearray[2] + '-' + datearray[1] + '-' + datearray[0];
		},
		ubahFormatNomral(val){
			if(val == null || val == ''){
				return '';
			}
			var date = val;
			var datearray = date.split("-");

			return datearray[2] + '-' + datearray[1] + '-' + datearray[0];
		},
		isInRange(val,start,end){
			val = new Date(val);
			start = new Date(start);
			end = new Date(end);

			if(val >= start && val <= end) {
				return true;
			} else {
				return false;
			}
		},
		ubahFormat2(val){
			var date = val;
			var datearray = date.split("/");
			if(datearray[1] < 10 || datearray[0] < 10){
				return datearray[2] + '-'+datearray[0] + '-' +"0"+ datearray[1];
			}

			return datearray[2] + '-' + datearray[0] + '-' + datearray[1];
		},
		plus(){

			this.nowDate.setDate(this.nowDate.getDate() + 1);
			var newDay = this.nowDate.toLocaleDateString();

			this.theDate = this.ubahFormat2(newDay)
			// var test = this.ubahFormat2(newDay)
			// console.log(test)

		},
		min(){

			this.nowDate.setDate(this.nowDate.getDate() - 1);
			var newDay = this.nowDate.toLocaleDateString();

			this.theDate = this.ubahFormat2(newDay)
			// var test = this.ubahFormat2(newDay)
			// console.log(test)

		},
	},
	mounted(){

		var now = new Date();

		var day = ("0" + now.getDate()).slice(-2);
		var month = ("0" + (now.getMonth() + 1)).slice(-2);

		var today = now.getFullYear()+"-"+(month)+"-"+(day) ;
		this.theDate = today;

		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/sedang-cuti';
		}
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				this.dataCuti = response.data;
				console.log(response.data);
			}).catch((error) => {
				console.log(error)
			});
		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		float:right;
		font-size:20pt;
		margin-top:7px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>