<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Status Pengajuan</b>
			</div>
		</div>
		<div class="container mt-5 mb-3 w-100">
			<div class="d-flex justify-content-between pt-4">
				<a href="javascript:void(0)" v-on:click="menunggu" class="btn btn-primary" id="menunggu">menunggu</a>
				<a href="javascript:void(0)" v-on:click="ditolak" class="btn btn-secondary" id="ditolak">ditolak</a>
				<a href="javascript:void(0)" v-on:click="disetujui" class="btn btn-secondary" id="disetujui">disetujui</a>
			</div>
			<div class="text-center" v-if="isLoaded == false">
				<span class="line mt-4" style="width:100%; height:50px;" v-if="isLoaded == false"></span>
				<span class="line mt-4" style="width:100%; height:50px;" v-if="isLoaded == false"></span>
			</div>
			<div class="card bg-white shadow mt-4" v-for="data in dataCuti" v-if="data.status == tipeStatus">
				<div class="card-body">
					<div class="row">
						<div class="col">
							<h5 class="card-title text-uppercase text-muted mb-0">Tipe</h5>
							<span class="h2 font-weight-bold mb-0">{{data.tipe_cuti}}</span>
						</div>
						<div class="col-auto">
							<div class="p-2 bg-warning text-white rounded-circle shadow" v-if="data.status == 'menunggu'"></div>
							<div class="p-2 bg-danger text-white rounded-circle shadow" v-if="data.status == 'ditolak'"></div>
							<div class="p-2 bg-success text-white rounded-circle shadow" v-if="data.status == 'disetujui'"></div>
						</div>
					</div>
					<div class="text-nowrap mt-3">{{data.mulai_cuti}} <span v-if="data.selesai_cuti != null">- {{data.selesai_cuti}}</span> ({{data.lama_cuti}} Hari)</div>
				</div>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";
import $ from 'jquery';

export default{
	data(){
		return{
			user: '',
			isLoaded: false,
			dataCuti:'',
			tipeStatus: 'menunggu'
		}
	},
	methods:{
		menunggu(){
			this.tipeStatus = 'menunggu'
			$("#menunggu").removeClass("btn btn-secondary");
			$("#menunggu").addClass("btn btn-primary");

			$("#disetujui").removeClass("btn btn-primary");
			$("#disetujui").addClass("btn btn-secondary");

			$("#ditolak").removeClass("btn btn-primary");
			$("#ditolak").addClass("btn btn-secondary");
		},
		ditolak(){
			this.tipeStatus = 'ditolak'
			$("#ditolak").removeClass("btn btn-secondary");
			$("#ditolak").addClass("btn btn-primary");

			$("#menunggu").removeClass("btn btn-primary");
			$("#menunggu").addClass("btn btn-secondary");

			$("#disetujui").removeClass("btn btn-primary");
			$("#disetujui").addClass("btn btn-secondary");
		},
		disetujui(){
			this.tipeStatus = 'disetujui'
			$("#disetujui").removeClass("btn btn-secondary");
			$("#disetujui").addClass("btn btn-primary");

			$("#menunggu").removeClass("btn btn-primary");
			$("#menunggu").addClass("btn btn-secondary");

			$("#ditolak").removeClass("btn btn-primary");
			$("#ditolak").addClass("btn btn-secondary");
		},
	},
	mounted(){
		if(localStorage.getItem('access') == "admin"){
			location.href = '/admin'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/status-pengajuan';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				if(response.status == 200){
					console.log(response.data);
					this.dataCuti = response.data;
				}
			}).catch((error) => {
				console.log(error)
			});

			

		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		float:right;
		font-size:20pt;
		margin-top:7px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>