<template>
  <div id="app" class="bg-white">
    <nav class="navbar fixed-bottom bg-white shadow tabbar-navbar" v-if="isLoggedIn">
      <ul class="nav d-flex justify-content-between w-100">
        <li class="nav-item text-center mb--3">
          <router-link to="/admin" class="nav-link" href="#" v-if="userRole == 'admin'">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-archive-2"></i></div>
            Home
          </router-link>
          <router-link to="/user" class="nav-link" href="#" v-if="userRole == 'user'">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-archive-2"></i></div>
            Home
          </router-link>
        </li>
        <li class="nav-item text-center mb--3">
          <router-link to="/cuti-admin" class="nav-link" href="#" v-if="userRole == 'admin'">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-briefcase-24"></i></div>
            Cuti
          </router-link>
          <router-link to="/cuti-user" class="nav-link" href="#" v-if="userRole == 'user'">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-briefcase-24"></i></div>
            Cuti
          </router-link>
        </li>
        <li class="nav-item text-center mb--3" v-if="userRole == 'admin'">
          <router-link to="/daftar-user"class="nav-link" href="#">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-single-02"></i></div>
            User
          </router-link>
        </li>
        <li class="nav-item text-center mb--3">
          <router-link to="/profile"class="nav-link" href="#">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-circle-08"></i></div>
            Profil
          </router-link>
        </li>
        <li class="nav-item text-center mb--3" v-if="userRole == 'admin'">
          <router-link to="/pengaturan"class="nav-link" href="#">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-settings-gear-65"></i></div>
            Setting
          </router-link>
        </li>
        <li class="nav-item text-center mb--3" v-if="userRole == 'user'">
          <a href="#" class="nav-link" @click="logout">
            <div class="m-1 icon-navigation m--2"><i class="ni ni-button-power"></i></div>
            Keluar
          </a>
        </li>
      </ul>
    </nav>

    <router-view/>
  </div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";

export default {
  data(){
    return{
      username: '',
      cuti: '',
      isLoaded:false,
      userRole: localStorage.getItem('access'),
    }
  },
  name: 'app',
  created: function () {
    this.$http.interceptors.response.use(undefined, function (err) {
      return new Promise(function (resolve, reject) {
        if (err.status === 401 && err.config && !err.config.__isRetryRequest) {
          this.$store.dispatch(logout)
        }
        throw err;
      });
    });
  },
  computed : {
    isLoggedIn : function(){ return this.$store.getters.isLoggedIn}
  },
  methods: {
    logout: function () {
      swal.fire({
        title: 'Konfirmasi',
        type: 'question',
        html:
        'Anda yakin akan keluar?',
        showCancelButton: true,
        focusConfirm: false,
        confirmButtonText:"Keluar",
        cancelButtonText:'Batal',
      }).then((result) => {
        if(result.value){
          this.$store.dispatch('logout')
          .then(() => {
            this.$router.push('/')
          })
        }
      });
      
    },
  },
  mounted(){
    var token = localStorage.getItem('token');

    var config = {
      headers: {'Authorization': "bearer " + token}
    };

    if(localStorage.getItem('access') == "admin"){
      var respLink = 'http://localhost:8000/api/admin/home';
    }
    if(localStorage.getItem('access') == "user"){
      var respLink = 'http://localhost:8000/api/user/home';
    }
    axios.get( 
      respLink,
      config
      ).then((response) => {
        this.isLoaded = true;
        this.username = response.data.data[0].name
        this.cuti = response.data.tipeCuti;
      }).catch((error) => {
        console.log(error)
      });
    }
  }

  </script>

