<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/pengaturan" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Reset Cuti</b>
			</div>
		</div>
		<div class="container mt-6">
			<div class="card shadow">
				<div class="card-header text-center">
					<i class="fa fa-exclamation-triangle" aria-hidden="true"></i> <b> Peringatan </b> <i class="fa fa-exclamation-triangle" aria-hidden="true"></i>

				</div>
				<div class="card-body text-center">
					<p>Dengan menekan tombol reset maka semua sisa cuti karyawan akan kembali seperti semula.</p>
					<p>
						Dan juga data pengajuan cuti, data cuti ditolak, dan data cuti diterima akan dihapus dan kembali seperti semula.
					</p>
				</div>
			</div>
			<div class="card mt-3 shadow text-center">
				<div class="card-header">
					<b>Reset Cuti</b>
				</div>
				<div class="card-body">
					<button class="btn btn-block btn-primary" v-on:click="resetCuti">Reset</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			dataCuti: '',

		}
	},
	methods:{
		resetCuti(){
			var startRequest = this.startReq;
			Swal.fire({
				title: 'Konfirmasi',
				type: 'question',
				html:
				'Anda yakin akan mereset semua data cuti?',
				showCancelButton: true,
				focusConfirm: false,
				confirmButtonText:"Reset",
				cancelButtonText:'Batal',
			}).then(function(result){
				if(result.value){
					startRequest = true;
					if(startRequest){
						Swal.fire({
							title: 'Memproses',
							html: 'Tunggu Sebentar ...',
							allowOutsideClick: false,
							onBeforeOpen: () => {
								Swal.showLoading()
							}
						})
					}
					var token = localStorage.getItem('token');

					var config = {
						headers: {'Authorization': "bearer " + token}
					};

					if(localStorage.getItem('access') == "admin"){
						var respLink = 'http://localhost:8000/api/admin/reset-cuti';
					}

					axios.get( 
						respLink,
						config
						).then((response) => {
							console.log(response.data)
							if(response.status == 200){
								Swal.fire({
									type:"success",
									title: "Sukses!",
									text: "Data berhasil di reset!",
								}).then(function(status){
									if(status.value){
										
									}
								})
							}
						}).catch((error) => {
							console.log(error)
						});
					}
				})
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/get-tipe-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.dataCuti = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>