<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary pb-4">
			<div class="text-center p-4 text-white">

			</div>
		</div>
		<div class="container mt--4 mb-3 w-100">
			<div class="card bg-white  shadow">
				<div class="card-body text-center">
					<b>Daftar User</b> <span class="badge bg-primary text-white ml-1">{{totalUser}}</span>
				</div>
			</div>
			<div class="row mt-4">
				<div class="col-12">
					<router-link to="/daftar-user/tambah-user" class="btn btn-primary btn-block"><i class="fa fa-plus mr-1"></i> Tambah User</router-link>
				</div>
			</div>
			<div class="input-group input-group-alternative mb-3 mt-3 shadow">
				<div class="input-group-prepend">
					<span class="input-group-text"><i class="fas fa-search"></i></span>
				</div>
				<input class="form-control" v-model="keywords" placeholder="Cari nama" type="text" required>
			</div>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			
			<div class="mt-4 myaccordion" v-if="isLoaded" v-for="users,i in filterData"  id="accordion">
				<a href="javascript:void(0)" class="nav-full d-block w-100 p-3 shadow mt-3 card text-muted dropdown-toggle" data-toggle="collapse" :data-target="'#demo' + users.id" :data-controls="'#demo' + users.id"  aria-expanded="false" :style="users.id == userId ? 'border:1px solid #f7736c' : ''">
					<i class="ni ni-circle-08 mr-3 float-left" style="font-size:40pt;"></i>
					<h3 class="m-0">{{ toTitleCase(users.name) }}</h3> ({{users.nama_divisi}})
				</a href="javascript:void(0)">
				<div :id="'demo' + users.id" class="collapse hide" :aria-labelledby="'demo' + users.id"  data-parent="#accordion">
					<div class="border-primary bg-white p-2 shadow pt-4" :style="users.id == userId ? 'border:1px solid #f7736c' : ''">
						<div class="table-responsive">
							<table class="table">
								<tbody>
									<tr>
										<th scope="row">Nama</th>
										<td>{{users.name}}</td>
									</tr>
									<tr>
										<th scope="row">Email</th>
										<td>{{users.email}}</td>
									</tr>
									<tr>
										<th scope="row">NIK</th>
										<td>{{users.nik}}</td>
									</tr>
									<tr>
										<th scope="row">Divisi / Departemen</th>
										<td>{{users.nama_divisi}}</td>
									</tr>
								</tbody>
							</table>
							<div class="mt-3 row pt-2 pb-4">
								<div class="col-4" v-if="isLoaded == false">
									<div class="pt-2 pb-1 text-center">
										<span class="line" style="width:25px;height:25px;" v-if="isLoaded == false"></span>
									</div>
								</div>
								<div class="col-4" v-if="isLoaded == false">
									<div class="pt-2 pb-1 text-center">
										<span class="line" style="width:25px;height:25px;" v-if="isLoaded == false"></span>
									</div>
								</div>
								<div class="col-4" v-if="isLoaded == false">
									<div class="pt-2 pb-1 text-center">
										<span class="line" style="width:25px;height:25px;" v-if="isLoaded == false"></span>
									</div>
								</div>
								<div class="col-4" v-if="isLoaded" v-for="(val,key) in jumCuti[i]">
									<div class="pt-2 pb-1 text-center">
										<div class="mb--1 h4">{{val}}</div>
										<small>{{key}}</small>
									</div>
								</div>
							</div>
							<router-link :to="{name: 'EditSisa', params:{id:users.id}}" class="btn btn-primary btn-block mb-3"><i class="fa fa-edit"></i> Edit Sisa Cuti</router-link>
							<div class="row">
								<div class="col-6" v-if="userId != users.id">
									<router-link :to="{name: 'EditUser', params:{id:users.id}}" class="btn btn-primary btn-block"><i class="fa fa-edit"></i> Edit User</router-link>
								</div>
								<div class="col-6" v-if="userId != users.id">
									<a href="javascript:void(0)" v-on:click="hapusUser(users.id,i)" class="btn btn-primary btn-block"><i class="fa fa-trash"></i> Hapus</a>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";
import $ from 'jquery';

export default{
	data(){
		return{
			user: '',
			isLoaded: false,
			totalUser:'',
			jumCuti: '',
			keywords: '',
			userId:'',
		}
	},
	computed:{
		filterData: function (){
			if(this.isLoaded){
				var user = this.user;
				return user.filter(users => {
					return users.name.match(this.keywords);
				})
			}
		}
	},
	methods:{
		toTitleCase(str) {
			return str.replace(/\w\S*/g, function(txt){
				return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
			});
		},
		hapusUser(userId,i){
			var isLoaded = this.isLoaded;
			var startReq = this.startReq;
			Swal.fire({
				title: 'Konfirmasi',
				type: 'question',
				html:
				'Anda yakin akan menghapus user ini?',
				showCancelButton: true,
				focusConfirm: false,
				confirmButtonText:"Hapus",
				cancelButtonText:'Batal',
			}).then(function(result){
				if(result.value){
					startReq = true;
					if(startReq){
						Swal.fire({
							title: 'Memproses',
							html: 'Tunggu Sebentar ...',
							allowOutsideClick: false,
							onBeforeOpen: () => {
								Swal.showLoading()

							}
						})
					}
					console.log(userId);
					var token = localStorage.getItem('token');

					var config = {
						headers: {'Authorization': "bearer " + token}
					};

					if(localStorage.getItem('access') == "admin"){
						var respLink = 'http://localhost:8000/api/admin/view_users/delete/'+userId;
					}

					axios.get( 
						respLink,
						config
						).then((response) => {
							console.log(response.data)
							isLoaded = true
							if(response.status == 200){
								console.log("deleted");
								Swal.fire({
									type:"success",
									title: "Sukses!",
									text: "User berhasil dihapus!",
								}).then(function(status){
									if(status.value){
										window.location.reload();
									}
								})
							}
						}).catch((error) => {
							console.log(error)
						});
					}
				})
		}
	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/view_users';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.user = response.data.cutiUser
					console.log(this.user);
					this.totalUser = this.user.length;
					this.jumCuti = response.data.jumlah;
					this.userId = response.data.userId;
				}
			}).catch((error) => {
				console.log(error)
			});

			$("#accordion").on("hide.bs.collapse show.bs.collapse", e => {
				$(e.target)
				.prev()
				.find("i:last-child")
				.toggleClass("fa-minus fa-plus");
			});

		},
		
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		float:right;
		font-size:20pt;
		margin-top:7px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>