<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary pb-4">
			<div class="text-center p-4 text-white">

			</div>
		</div>
		<div class="container mt--4 mb-3 w-100">
			<div class="card bg-white  shadow">
				<div class="card-body text-center">
					<b>Data Cuti</b>
				</div>
			</div>
			<div class="mt-4">
				<router-link to="/cuti-admin/konfirmasi-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-check-bold mr-3"></i>
					Konfirmasi Cuti <span class="badge bg-primary text-white ml-1" v-if="isLoaded && jumlah.pending != 0">{{jumlah.pending}}</span>
					<i class="fa fa-circle-o-notch fa-spin ml-1 text-primary" style="font-size:14px" v-if="!isLoaded"></i>
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
				<router-link to="/cuti-admin/sedang-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-spaceship mr-3"></i>
					Sedang Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
				<router-link to="/cuti-admin/status-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-calendar-grid-58 mr-3"></i>
					Status Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
				<router-link to="/cuti-admin/ajukan-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-time-alarm mr-3"></i>
					Ajukan Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
				<router-link to="/cuti-admin/tipe-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="fa fa-plus mr-3"></i>
					Tipe Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
export default{
	data(){
		return{
			jumlah: '',
			isLoaded: false,
		}
	},
	methods:{

	},
	mounted(){
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/jumlah';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.jumlah = response.data
					
				}
			}).catch((error) => {
				console.log(error)
			});
		}
	}
	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background: #F7736C;
		color: #fff !important;
	}
	</style>