<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/cuti-user/ajukan-cuti" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Kebijakan Cuti</b>
			</div>
		</div>
		<div class="container mt-6">
			<div class="card mt-3 shadow">
				<div class="card-header text-center">
					<b>Kebijakan Cuti</b>
				</div>
				<div class="card-body p-4">
					<p v-if="!isEditing">
						<span v-html="kebijakanCuti"></span>
						<span class="line text-center mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
						<span class="line text-center mt-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
						<span class="line text-center mt-3 mb-3" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
					</p>
					<form @submit.prevent="updateCuti" :style="!isEditing ? 'display:none;' : '' ">
						<textarea id="froala-editor">
							{{kebijakanCuti}}
						</textarea>

					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			kebijakanCuti: '',
			quil:'',
			isEditing:false,

		}
	},
	methods:{
		
	},
	mounted(){

		if(localStorage.getItem('access') == "admin"){
			location.href = '/admin'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/kebijakan-cuti';
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.kebijakanCuti = response.data
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	#editor-container {
		height: 120px;
	}


	</style>