<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/daftar-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Edit Sisa Cuti {{user.name}}</b>
			</div>
		</div>
		<div class="container mt-6">
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<span class="line mt-4" style="width:100%; height:20px;" v-if="isLoaded == false"></span>
			<form @submit.prevent="tambahTipe">
				<div class="input-group input-group-merge shadow mt-3" v-for="(val,key) in dataCuti">
					<input name="inputtipe[]" class="form-control bg-secondary text-muted" id="inputtipe" placeholder="Tipe Cuti" type="text" style="border-right:1px solid #eaeaea;" :value="key" readonly required>
					<div class="input-group-append">
						<input name="inputjum[]" class="form-control bg-white" id="inputJumlah" style="width:100px;border-radius:0px !important;" placeholder="Lama" :value="val" width="10px" required>
					</div>
				</div>
				<input type="submit" name="send" class="btn btn-primary btn-block mt-3" value="Ubah">
			</form>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			tipeCutiInput: '',
			lamaCutiInput: '',
			isLoaded: false,
			idConfirm: '',
			startReq: false,
			dataCuti: '',
			user: '',
			startReq: false

		}
	},
	methods:{
		tambahTipe(){
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()
					}
				})
			}
			var token = localStorage.getItem('token')
			var keys  = $('#inputtipe[name^=inputtipe]').map(function(idx, elem) {
				return $(elem).val();
			}).get();
			var values  = $('#inputJumlah[name^=inputjum]').map(function(idx, elem) {
				return $(elem).val();
			}).get();

			var bodyTipe = {};
			keys.forEach((key, i) => bodyTipe[key] = values[i]);
			console.log(bodyTipe);
			axios({url: 'http://localhost:8000/api/admin/edit-sisa-cuti', headers:{Authorization: "Bearer " + token},data: bodyTipe, method: 'POST' })
			.then(resp => {
				console.log(resp.data);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Data berhasil diperbaharui!",
					allowOutsideClick: false,
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
			}).catch(error => {
				console.log(error)
				this.startReq = false
			});
		},
	},
	mounted(){
		var userId = this.$route.params.id;
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/get-sisa-cuti/'+userId;
		}
		
		axios.get( 
			respLink,
			config
			).then((response) => {
				console.log(response.data)
				this.isLoaded = true
				if(response.status == 200){
					this.dataCuti = response.data.sisa
					this.user = response.data.user[0]
				}
			}).catch((error) => {
				console.log(error)
			});
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>