<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary fixed-top shadow">
			<div class="text-center p-3 text-white">
				<router-link to="/daftar-user" style="color:#fff !important;font-size:18pt;margin-left:-20px;" class="float-left mt--1"><i class="ni ni-bold-left"></i></router-link> <b>Edit User</b>
			</div>
		</div>
		<div class="col-xl-4 order-xl-2 mb-5 mb-xl-0 mt-8">
			<div class="card card-profile shadow">
				<div class="row justify-content-center">
					<div class="col-lg-3 order-lg-2">
						<div class="card-profile-image">
							<img src="/assets/img/avatar.png" class="rounded-circle" style="border:5px solid #f7736c;">
						</div>
					</div>
				</div>
				<div class="text-center">
					<div class="mt-9" v-if="!isLoaded"></div>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
					<span class="line mt-3 mb-3" style="width:80%; height:20px;" v-if="isLoaded == false"></span>
				</div>
				<div class="card-body pt-0 pt-md-4" v-if="isLoaded == true">
					<div class="text-center mt-9"></div>
					<form @submit.prevent="ubahProfile">
						<div class="text-center mt-4">
							<h3>
								<span v-if="!editing">{{user.name}}<span class="font-weight-light"></span></span>
								<input type="text" v-model="user.name" class="form-control" v-if="editing">
							</h3>
						</div>
						<div class="text-center">

							<div class="h5 font-weight-300">
								<span v-if="!editing"><i class="ni location_pin mr-2"></i>{{user.alamat}}</span>
								<input type="text" v-model="user.alamat" class="form-control" v-if="editing">
							</div>
							<div>
								<span v-if="!editing"><i class="ni education_hat mr-2"></i>NIK : {{user.nik}}</span>
								<input type="text" v-model="user.nik" class="form-control" v-if="editing">
							</div>
							<hr class="my-4">
							<span v-if="!editing"><a href="#">{{user.email}}</a></span>
							<input type="text" v-model="user.email" class="form-control" v-if="editing">
						</div>
						<hr>
						<div class="text-center d-flex justify-content-between">
							<a href="javascript:void(0)" class="btn btn-block btn-primary" v-on:click="editProfile()" v-if="!editing">Ubah Profil User</a>
							<button  type="submit" class="btn w-50 btn-primary" v-if="editing">Ubah</button>
							<a href="javascript:void(0)" class="btn w-50 btn-secondary" v-on:click="batalEditProfile()" v-if="editing">Batal</a>
						</div>
					</form>
					
				</div>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";

export default{
	data(){
		return{
			user: '',
			isLoaded: false,
			editing:true,
			tipeDivisi: '',
			startReq: false,
		}
	},
	methods:{
		editProfile(){
			this.editing = true
			console.log(this.editing)
		},
		batalEditProfile(){
			this.editing = false
			console.log(this.editing)
		},
		ubahProfile(){
			this.startReq = true;
			if(this.startReq){
				Swal.fire({
					title: 'Memproses',
					html: 'Tunggu Sebentar ...',
					allowOutsideClick: false,
					onBeforeOpen: () => {
						Swal.showLoading()
					}
				})
			}
			var token = localStorage.getItem('token')
			console.log(this.user);
			var bodyReq = this.user;
			axios({url: 'http://localhost:8000/api/admin/user/edit', headers:{Authorization: "Bearer " + token},data: bodyReq, method: 'POST' })
			.then(resp => {
				console.log(resp);
				Swal.fire({
					type:"success",
					title: "Sukses!",
					text: "Data berhasil diperbaharui!",
					allowOutsideClick: false,
				}).then(function(status){
					if(status.value){
						location.reload();
					}
				})
			}).catch(error => {
				console.log(error)
				this.startReq = false
			});
		},
	},
	mounted(){
		
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};
		var userId = this.$route.params.id;	
		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/user/edit/'+userId;
		}

		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true
				if(response.status == 200){
					this.user = response.data.cutiUser[0]
					this.tipeDivisi = response.data.tipe;
					console.log(response.data);
				}
			}).catch((error) => {
				console.log(error)
			});

		}
	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}
	.nav-full:active{
		background:#F7736C;
		color: #fff !important;
	}
	.dropdown-toggle[aria-expanded="true"]:after {
		transform: rotate(0deg);
	}


	.dropdown-toggle:after {
		float:right;
		font-size:20pt;
		margin-top:7px;
		transform: rotate(-90deg);
		transition: 0.5s;
	}

	</style>