<template>
	<div class="fixmargin">
		<div class="container bg-gradient-primary pb-4">
			<div class="text-center p-4 text-white">
				
			</div>
		</div>
		<div class="container mt--4 mb-3 w-100">
			<div class="card bg-white text-center shadow">
				<div class="card-body text-center">
					<b>Pengaturan</b>
				</div>
			</div>
		</div>
		<div class="container">
			<div class="mt-3">
				<router-link to="/pengaturan/email-aktif-admin" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-email-83 mr-3"></i>
					Email Aktif Admin
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
			</div>
			<div class="mt-3">
				<router-link to="/pengaturan/Kebijakan-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-paper-diploma mr-3"></i>
					Kebijakan Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
			</div>
			<div class="mt-3">
				<router-link to="/pengaturan/reset-cuti" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="fa fa-refresh mr-3"></i>
					Reset Cuti
					<i class="ni ni-bold-right float-right mt-1"></i>
				</router-link>
			</div>
			<div class="mt-3">
				<a href="#" @click="logout" class="nav-full d-block p-3 shadow mt-3 card text-muted">
					<i class="ni ni-button-power mr-3"></i>
					Keluar
				</a>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import swal from "sweetalert2";

export default{
	data(){
		return{
			jumCuti: '',
			tipeCuti: '',
			jumlah: '',
			username: "",
			isLoaded: false,
			divisi: '',
			selamat:'',
			surat:'',

			editEmail: false,
		}
	},
	methods:{
		logout: function () {
			swal.fire({
				title: 'Konfirmasi',
				type: 'question',
				html:
				'Anda yakin akan keluar?',
				showCancelButton: true,
				focusConfirm: false,
				confirmButtonText:"Keluar",
				cancelButtonText:'Batal',
			}).then((result) => {
				if(result.value){
					this.$store.dispatch('logout')
					.then(() => {
						this.$router.push('/')
					})
				}
			});

		},
		getDate: function () {
			var greeting;
			var time = new Date().getHours();
			if (time < 10) {
				greeting = "Selamat Pagi";
			} else if (time < 14) {
				greeting = "Selamat Siang";
			} else if(time < 19){
				greeting = "Selamat Sore";
			}else {
				greeting = "Selamat Malam";
			}
			this.selamat = greeting;
			console.log(time);

		},
	},
	mounted(){
		this.getDate();
		if(localStorage.getItem('access') == "user"){
			location.href = '/user'
		}
		var token = localStorage.getItem('token');

		var config = {
			headers: {'Authorization': "bearer " + token}
		};
		var configQuran = {
			headers: {
				
			},
		};

		if(localStorage.getItem('access') == "admin"){
			var respLink = 'http://localhost:8000/api/admin/home';
		}
		if(localStorage.getItem('access') == "user"){
			var respLink = 'http://localhost:8000/api/user/home';
		}
		axios.get( 
			respLink,
			config
			).then((response) => {
				this.isLoaded = true;
				console.log(response.data);
				this.username = response.data.data[0].name
				this.divisi = response.data.data[0].divisi
				this.jumCuti = response.data.jumlah;
				this.tipeCuti = response.data.tipe;
				this.jumlah = response.data.jumlah;
			}).catch((error) => {
				console.log(error)
			});

			// fetch('')
			// .then(response => response.json())
			// .then(json => console.log(json))
			// fetch(' https://api.banghasan.com/quran/format/json/acak')
			// .then((response) => response.json())
			// .then((messages) => {
			// 	console.log(messages);
			// 	this.surat = messages;
			// });
		}

	}

	</script>

	<style scoped>
	.fixmargin{
		margin-bottom:60px;
	}

	</style>